<template>
  <div class="container about">
    <TheTitle :theme="theme" :title="title" />
    <NavAbout 
      @getBtnIndex="getBtnIndex"
      :currentPage="currentPage"
    />
    <div class="sections-to-show" v-show="currentPage === 0">
      <HistoryCompany />
    </div>
    <div class="sections-to-show" v-show="currentPage === 1">
      <OurTeam />
    </div>
    <div class="sections-to-show" v-show="currentPage === 2">
      <OurGoals />
    </div>
    <div class="sections-to-show" v-show="currentPage === 3">
      <Certificates />
    </div>
  </div>
</template>

<script>
import TheTitle from '../components/The-title'
import NavAbout from '../components/ABOUT/Nav-about'
import HistoryCompany from '../components/ABOUT/History-company'
import OurTeam from '../components/ABOUT/Our-team'
import OurGoals from '../components/ABOUT/Our-goals'
import Certificates from '../components/ABOUT/Certificates'

export default {
  name: 'About',
  components: {
    TheTitle,
    NavAbout,
    HistoryCompany,
    OurTeam,
    OurGoals,
    Certificates
  },
  data() {
    return {
      theme: 'Main Menu / About Us',
      title: 'About Us',
      currentPage: 0,
    }
  },
  methods: {
    getBtnIndex(index) {
      this.currentPage = index
    }
  },
};
</script>

<style lang="scss">
@mixin media-size($size){
  @media(max-width: $size){
    @content
  }
}

.about{
  min-height: 100vh;
  padding: 220px 0 70px;
  // Media
  @include media-size(1230px) {
    padding: 170px 0 30px;
  }
  @include media-size(790px) {
    padding: 150px 0 30px;
  }
}
</style>